.page {
  font-family: Cinzel, serif;
  line-height: 140%;
  color: $main-text-color;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    padding: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;
    color: $accent-text-color;

    &:target {
      opacity: 1;
      transform: translateX(-0%);
      pointer-events: all;
    }
  }
}
