.menu {
  box-sizing: border-box;
  overflow: auto;
  height: 100vh;
  color: $accent-text-color;
  background-color: $accent-bg-color;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 16px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: $accent-bg-color;
  }

  @include on-tablet {
    margin-bottom: 0;
  }

  &__nav {
    margin-bottom: 32px;
  }

  &__phone-number {
    display: block;
    position: relative;
    color: inherit;
    text-decoration: none;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    text-align: left;
    margin-bottom: 20px;
    &::after {
      content: '';
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 11%;
      background-color: $accent-text-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }

  &__call-to-order {
    display: block;
    position: relative;
    color: inherit;
    text-decoration: none;
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 2px;
    text-align: left;
    padding-bottom: 8px;
    width: fit-content;
    &::after {
      content: '';
      position: absolute;
      bottom: -8px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $accent-text-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}
